.Dialogues {
    display: grid;
    grid-template-columns: 3fr 9fr;
}

.dialogItems {
    margin: 15px;

}
.dialogItems .active{
    color: #f25371;
}

.dialog {
    padding: 10px;
    display: flex;
}
.dialog a{
   text-decoration: none;
    color: #031ecd;
}

.Messages {
    margin: 15px;
    justify-content: center;
}

.message {
    padding: 10px;
    width: 600px;
    min-height: 30px;
    border: dotted 1px #434141;
    margin: 10px;
    color: white;
}
.dialog  img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.messageArea{
    text-align: center;
}
textarea{
    width: 250px;
    min-height: 40px;
    padding: 7px;
    border-radius: 3px;
}

button{
    padding: 5px;
    border-radius: 3px;
    background-color: #f8d0ec;
    border: none;
}